<template>
  <div class="SharingBoxS">
    <div class="container">
      <div class="heard" style="margin-bottom:30px">
        <!-- <grade /> -->
      </div>
      <div class="left">
        <subject class="subj" />
      </div>
      <div class="right">
        <div class="imgbox">
          <img src="../../../../static/images/微信图片_20191205174809.png" alt />
        </div>
        <div class="workList">
          <table>
            <thead>
              <tr style="background-color:#f8f8f8">
                <td style="color:#2ab8b3;font-size:16px;padding:5px">序号</td>
                <td style="color:#2ab8b3;font-size:16px;padding:5px">作业名称</td>
                <td style="color:#2ab8b3;font-size:16px;padding:5px">题型</td>
                <td style="color:#2ab8b3;font-size:16px;padding:5px">难度</td>
                <td style="color:#2ab8b3;font-size:16px;padding:5px">来源</td>
                <td style="color:#2ab8b3;font-size:16px;padding:5px">时间</td>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item,index) in hwlist "
                :key="index"
                @click="chooseHomework(item)"
                class="trHover"
              >
                <td style="padding:10px">{{ index+1 }}</td>
                <td style="padding:10px;color:#f88869">内能</td>
                <td style="padding:10px">综合考试</td>
                <td style="padding:10px">中级</td>
                <td style="padding:10px">小陈老师</td>
                <td style="padding:10px">2019-12-6</td>
              </tr>
            </tbody>
          </table>
          <paginationVue class="page" />
        </div>
      </div>
    </div>
    <div class="takeWork">
      <div class="homeWork">
        <img src="../../../../static/images/微信图片_20191205174831.png" alt />
      </div>
      <div class="workNum">
        <div class="listOne">
          <div class="listBoxs">
            <div class="border">
              <p>36</p>
            </div>
            <span>
              <img src="../../../../static/images/总人数，用户.png" alt />
            </span>
            <span>总人数</span>
          </div>
        </div>
        <div class="listOne">
          <div class="listBoxs">
            <div class="border">
              <p>30</p>
            </div>
            <span>
              <img
                src="../../../../static/images/未完成盘点任务是否允许开单.png"
                alt
                style="width:18px;height:14px"
              />
            </span>
            <span>已完成人数</span>
          </div>
        </div>
        <div class="listOne">
          <div class="listBoxs">
            <div class="border">
              <p>6</p>
            </div>
            <span>
              <img
                src="../../../../static/images/未完成-流程-状态_jurassic.png"
                alt
                style="width:18px;height:14px"
              />
            </span>
            <span>未完成人数</span>
          </div>
        </div>
      </div>
      <div class="GradeBox">
        <div class="GradeOne">
          <div class="gradeImgBox">
            <img src="../../../../static/images/等级分布.png" alt />
            <el-dropdown class="dropdown">
              <span class="el-dropdown-link">
                选择班级
                <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>一年级一班</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
          <div class="tableBox">
            <table>
              <thead>
                <tr style="background-color:#f9f9f9">
                  <td rowspan="2">班级</td>
                  <td colspan="2" style="font-size:16px;font-weight: bold;">{A等:100~95}</td>
                  <td colspan="2" style="font-size:16px;font-weight: bold;">{B等:95~85}</td>
                  <td colspan="2" style="font-size:16px;font-weight: bold;">{C等:85~75}</td>
                  <td colspan="2" style="font-size:16px;font-weight: bold;">{D等:75~0}</td>
                </tr>
                <tr style="background-color:#f9f9f9">
                  <td style="padding:5px">班级</td>
                  <td style="padding:5px">人数</td>
                  <td style="padding:5px">班级</td>
                  <td style="padding:5px">人数</td>
                  <td style="padding:5px">班级</td>
                  <td style="padding:5px">人数</td>
                  <td style="padding:5px">班级</td>
                  <td style="padding:5px">人数</td>
                </tr>
              </thead>
              <tbody>
                <tr style="background-color:#fff8f1;">
                  <td style="padding:6px">全部班级</td>
                  <td style="padding:6px">44</td>
                  <td style="padding:6px">小陈</td>
                  <td style="padding:6px">44</td>
                  <td style="padding:6px">小陈</td>
                  <td style="padding:6px">44</td>
                  <td style="padding:6px">小陈</td>
                  <td style="padding:6px">44</td>
                  <td style="padding:6px">小陈</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <!-- 题目正确率 -->
        <div class="GradeOne">
          <div class="gradeImgBox">
            <img src="../../../../static/images/题目正确率.png" alt />
            <el-dropdown class="dropdown">
              <span class="el-dropdown-link">
                选择班级
                <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>一年级一班</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
          <div class="tableBox">
            <table>
              <thead>
                <tr style="background-color:#f9f9f9">
                  <td>题号</td>
                  <td style="padding:5px" v-for="(item,index) in nums " :key="index">第{{index+1}}题</td>
                </tr>
              </thead>
              <tbody>
                <tr style="background-color:#fff8f1;">
                  <td>正确率</td>
                  <td style="padding:5px" v-for="(item,index) in nums " :key="index">{{index+1}}%</td>
                </tr>
                <tr style="background-color:#fff8f1;">
                  <td>错题人数</td>
                  <td
                    style="padding:5px"
                    v-for="(item,index) in nums "
                    :key="index"
                    @click="dialogVisible = true"
                  >{{index+1}}人</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
        <!-- 学生正确率 -->
        <div class="GradeOne">
          <div class="gradeImgBox">
            <img src="../../../../static/images/学生正确率.png" alt />
            <el-dropdown class="dropdown">
              <span class="el-dropdown-link">
                选择班级
                <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item>一年级一班</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
          <div class="tableBox">
            <table>
              <thead>
                <tr style="background-color:#f9f9f9">
                  <td>姓名</td>
                  <td style="padding:5px" v-for="(item,index) in nums " :key="index">陈老师</td>
                </tr>
              </thead>
              <tbody>
                <tr style="background-color:#fff8f1;">
                  <td>正确率</td>
                  <td style="padding:5px" v-for="(item,index) in nums " :key="index">{{index+1}}%</td>
                </tr>
                <tr style="background-color:#fff8f1;">
                  <td>错题编号</td>
                  <td style="padding:5px" v-for="(item,index) in nums " :key="index">{{index+1}}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>

      <el-dialog title="错误人员" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
        <span>小陈</span>
        <span>小陈</span>
        <span>小陈</span>
        <span>小陈</span>
        <span>小陈</span>
        <span>小陈</span>
        <span>小陈</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import grade from "@/components/grade/grade.vue";
import subject from "@/components/grade/subject.vue";
import Utils from "../../../../static/js/util";
import paginationVue from "../../../components/pagination/pagination.vue";

export default {
  components: {
    paginationVue,
    subject,
    grade,
    Utils
  },
  data() {
    return {
      dialogVisible: false, //显示弹出框
      list: [1, 1, 1, 1, 1, 1],
      nums: [],
      hwlist: [1, 1, 1, 1, 1, 1, 1, 1],
      subjectId: "", //学科id
      textbookId: sessionStorage.getItem("textid"), //教科书id
      chapterId: "", //章id
      barId: "", //节id
      pageSize: 1,
      pageNum: 1,
      total: 0,
      value4: ""
    };
  },
  mounted() {
    this.getnum();
  },
  methods: {
    handleClose(done) {
      this.$confirm("确认关闭？")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    getnum() {
      let num = 10;
      for (var i = 0; i < num; i++) {
        this.nums.push(i);
      }
    },
    queryClassHomework() {
      Utils.$on("getData", res => {
        console.log(res);
      });
      Utils.$on("subjectId", res => {
        console.log(res, 222);
      });
      this.hwlist = [];
      // console.log()
      this.$post("/likework/queryClassHomework.do", {
        classId: this.value4, //班级id

        subjectId: this.subjectId, //学科id
        textbookId: "", //教科书id
        chapterId: this.chapterId, //章id
        barId: this.barId, //节id
        contentId: this.contentId, //内容id
        time: "",
        rows: 10,
        indexNo: this.pageNum,
        flag: 0 //发布状态 0-已发布 1-未发布
      }).then(response => {
        //console.log(response)
        if (response.code == "5000") {
          if (response.data.rows != "") {
            // console.log(response)
            this.hwlist = response.data.rows;
            this.total = response.data.pageCount;
          }
        }
      });
    }
  }
};
</script>

<style lang="less">
.SharingBoxS {
  .container {
    overflow: hidden;
    .left {
      width: 18%;
      height: 100%;
      display: inline-block;
      float: left;
      .subj {
        height: 100%;
      }
    }
    .right {
      background-color: #fff;
      display: inline-block;
      width: 81%;
      // border: 1px solid #bbb;
      overflow: hidden;
      float: right;
      .page {
        position: relative;
        right: -350px;
        bottom: 20px;
      }
      .imgbox {
        width: 100%;
        height: 62px;
        border-bottom: 1px solid #eee;
        img {
          margin-top: 17px;
        }
      }
      .workList {
        text-align: center;
        padding: 15px;
        table {
          width: 100%;
          .trHover:hover {
            background-color: #faf1ed;
          }
        }
      }
    }
  }
  .takeWork {
    border-top: 10px solid #f8f8f8;
    width: 100%;
    background-color: #fff;
    .GradeBox {
      margin-top: 10px;
      background-color: #fff;
      .GradeOne {
        border-bottom: 10px solid #f8f8f8;
        .gradeImgBox {
          .dropdown {
            margin-right: 150px;
            float: right;
            color: #fe8a00;
          }
        }
        .tableBox {
          margin-left: 240px;
          padding: 16px;
          position: relative;
          table {
            width: 70%;
            text-align: center;
            td {
              position: relative;
            }
          }
          .bubble {
            width: 150px;
            background-color: #655e53;
            border-radius: 15px;
            padding: 10px;
            position: absolute;
            right: 50%;
            top: 50%;
            span {
              color: #fff;
            }
          }
        }
      }
    }
    .homeWork {
      width: 100%;
      height: 56px;
      display: flex;
      justify-content: space-between;
      // background-color: #faf1ed;

      img {
        width: 110px;
        height: 30px;
        margin-top: 13px;
      }

      .NumBox {
        flex: 1;
        text-align: center;
        line-height: 56px;
        span {
          font-size: 18px;
          margin-right: 10px;
        }
      }
    }
    .workNum {
      width: 100%;
      height: 152px;
      background-color: #fff;
      border-bottom: 1px solid #eee;
      display: flex;
      justify-content: space-between;
      .listOne {
        width: 33.33%;
        text-align: center;
        // padding: 30px;
        .listBoxs {
          border-right: 1px solid #eee;
          .border {
            width: 100px;
            height: 100px;
            margin: 0 auto;
            border-radius: 50%;
            background-color: #a8e2f6;
            p {
              font-size: 40px;
              line-height: 100px;
            }
          }
        }
      }
    }
  }
}
</style>


